/* Landing page styles and theme using SuraSmart_logo.png
   Place SuraSmart_logo.png in frontend/public/images/ for this to work
*/

.home-container {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
  color: #f7f9fb;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: radial-gradient(ellipse at center, rgba(0,0,0,0.35), rgba(0,0,0,0.6));
}

/* Hero uses the logo as a subtle background, covered and centered */
.hero {
  flex: 1 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6rem 1.5rem;
  background-image: linear-gradient(rgba(0,0,0,0.45), rgba(0,0,0,0.45)), url('../../public/images/SuraSmart_logo.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #ffffff;
  text-align: center;
}

.hero-content h2 {
  font-size: 2.6rem;
  margin: 0 0 0.5rem 0;
}

.hero-content p {
  font-size: 1.1rem;
  margin-bottom: 1.25rem;
  opacity: 0.95;
}

.hero-btn, .cta-btn, .nav-btn {
  background: linear-gradient(90deg, #0ea5a5, #06b6d4);
  border: none;
  color: #042027;
  padding: 0.7rem 1.2rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
}

.hero-btn:hover, .cta-btn:hover, .nav-btn:hover {
  filter: brightness(1.05);
}

/* Navigation */
.home-nav {
  background: transparent;
  position: sticky;
  top: 0;
  z-index: 40;
  padding: 0.75rem 1rem;
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1100px;
  margin: 0 auto;
}

.nav-logo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.logo-img {
  width: 56px;
  height: 56px;
  object-fit: contain;
  border-radius: 8px;
  background: rgba(255,255,255,0.02);
  padding: 6px;
}

.logo-text h1 {
  margin: 0;
  font-size: 1.25rem;
  color: #ffffff;
}

.tagline {
  display: block;
  font-size: 0.8rem;
  color: rgba(255,255,255,0.85);
}

.features, .roles-section, .cta-section {
  max-width: 1100px;
  margin: 2.5rem auto;
  padding: 1rem;
  color: #eaf6f7;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1rem;
}

.feature-card, .role-card {
  background: rgba(255,255,255,0.03);
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 6px 18px rgba(2,6,23,0.45);
}

.home-footer {
  text-align: center;
  padding: 1.5rem 0;
  color: rgba(255,255,255,0.7);
}

@media (max-width: 640px) {
  .hero-content h2 { font-size: 1.8rem; }
}
